import React from 'react';
import mn from './Menu.module.css';
import {NavLink} from "react-router-dom";


const Menu = () => {
    return (
        <div className={mn.block}>
            <ul className={mn.menu}>
                <li className={mn.item}>
                    <NavLink to="/dashboard" activeClassName={mn.active} className={mn.link}>
                        Dashboard
                    </NavLink>
                </li>
                <li className={mn.item}>
                    <NavLink to="/messeges" activeClassName={mn.active} className={mn.link}>
                        Messeges
                    </NavLink>
                </li>
                <li className={mn.item}>
                    <NavLink to="/friends" activeClassName={mn.active} className={mn.link}>
                        Friends
                    </NavLink>
                </li>
                <li className={mn.item}>
                    <NavLink to="/music" activeClassName={mn.active} className={mn.link}>
                        Music
                    </NavLink>
                </li>
            </ul>
        </div>
    )
}

export default Menu;