import React from "react";
import ms from './Message.module.css';


const Message = (props) => {
    return (
        <div className={ms.messege__block}>
            <div className={ms.user__messege}>
                <img className={ms.userIcon} src={props.userIcon} alt=""/>
                <p>{props.messageText}</p>
            </div>
            <div className={ms.date}>
                {props.messageDate}
            </div>
        </div>
    )
}

export default Message;