let ADD_MESSAGE = 'ADD-MESSAGE';
let TYPE_MESSAGE = 'TYPE-MESSAGE';



let initialState = {
    messagesData: [
        {
            messageText: 'Hello my dear friend',
            messageDate: '11.04.2020 22:00',
            messageId: 1,
            userIcon: "https://image.flaticon.com/icons/png/512/21/21104.png",
        },
        {
            messageText: 'Hello my dear friend',
            messageDate: '11.04.2020 22:00',
            messageId: 2,
            userIcon: "https://image.flaticon.com/icons/png/512/21/21104.png",
        },
        {
            messageText: 'Hello my dear friend',
            messageDate: '11.04.2020 22:00',
            messageId: 3,
            userIcon: "https://image.flaticon.com/icons/png/512/21/21104.png",
        },
        {
            messageText: 'Hello my dear friend',
            messageDate: '11.04.2020 22:00',
            messageId: 4,
            userIcon: "https://image.flaticon.com/icons/png/512/21/21104.png",
        }

    ],
    newMessageText: 'Dimich',
    button: "Send a Message",
};

let dialogsReducer = (state = initialState, action) => {

    switch (action.type){
        case ADD_MESSAGE: {
            let newMessage = {
                messageText: state.newMessageText,
                messageDate: '11.04.2020 22:00',
                messageId: 1,
                userIcon: "https://image.flaticon.com/icons/png/512/21/21104.png",
            }
            return {
                ...state,
                messagesData: [...state.messagesData, newMessage],
                newMessageText: '',
            }
           /* let stateCopy = {...state};
            stateCopy.messagesData = [...state.messagesData];
            stateCopy.messagesData.push(newMessage);
            return stateCopy;*/
        }
        case TYPE_MESSAGE:
           /* let stateCopy = {...state};
            stateCopy.newMessageText = action.newText;
            return stateCopy;*/
            return {
                ...state,
            newMessageText: action.newText,
            }
        default: return state;
    }
}

export const addMessageActionCreator = () => ({type: ADD_MESSAGE});

export const typeMessageActionCreator = (text) => ({type: TYPE_MESSAGE, newText: text});

export default dialogsReducer;