import React from 'react';
import ab from './AvatarBlock.module.css';
import user from '../../img/user.jpg';

const AvatarBlock = (props) => {
    return (
        <div className={ab.main__block}>
            <img className={ab.main__user__img} src={user} alt=""/>
            <h3 className={ab.user__name}>{props.name}</h3>
            <p className={ab.subtext}>You need a hand? I have two.</p>
        </div>
    )
}

export default AvatarBlock;