import React from 'react';
import './App.css';
import Header from "./Header/Header";
import Main from "./Main/Main";
import {BrowserRouter} from "react-router-dom";



function App(props) {
  return (
    <BrowserRouter>
        <div className="App">
            <Header/>
            <Main dispatch={props.dispatch} state={props.state}/>
        </div>
    </BrowserRouter>
  );
}

export default App;
