let FOLLOW = 'FOLLOW';
let UNFOLLOW = 'UNFOLLOW';
let SET_USERS = 'SET_USERS';
let SET_PAGE_NUMBER = 'SET_PAGE_NUMBER';
let SET_TOTAL_COUNT = 'SET_TOTAL_COUNT';
let IS_FETCHING = 'IS_FETCHING';

let initialState = {
    pageSize: 12,
    totalCount: 20,
    currentPage: 1,
    isFetching: true,
    userInfo:[

    ],
};

let usersReducer = (state = initialState, action) => {
    switch(action.type){
        case FOLLOW:
            return {
                ...state,
                userInfo: state.userInfo.map(u => {
                    if (u.id === action.userId) {
                        return {...u, followed: true}
                    }
                    return {...u}
                }),
            }
        case UNFOLLOW:
            return {
                ...state,
                userInfo: state.userInfo.map(u => {
                    if (u.id === action.userId) {
                        return {...u, followed: false}
                    }
                    return {...u}
                }),
            }
        case SET_USERS:
            return {
                ...state,
                userInfo: [...action.userInfo],
            }
        case SET_PAGE_NUMBER:
            return {
                ...state,
                currentPage: action.pageNumber
            }
        case SET_TOTAL_COUNT:
            return {
                ...state,
                totalCount: action.totalCount,
            }
        case IS_FETCHING:
            return {
                ...state,
                isFetching: action.boolen,
            }
        default:
            return state;
    }
    return state;
}
export const follow = (id) => ({type: FOLLOW, userId: id});
export const unfollow = (id) => ({type: UNFOLLOW, userId: id});
export const setUsers = (users) => ({type: SET_USERS, userInfo: users});
export const setPageNumber = (number) => ({type: SET_PAGE_NUMBER, pageNumber: number});
export const setTotalCount = (count) => ({type: SET_TOTAL_COUNT, totalCount: count});
export const toggleIsFetching = (boolen) => ({type: IS_FETCHING, boolen: boolen});

export default usersReducer;