import React from "react";
import UserCard from "./UserCard/UserCard";
import u from './Users.module.css';
import userIcon from '../../img/user.jpg';
import Header from "../../Header/Header";


const Users = (props) => {


    let pagesButtons = [];
    let pagesCount = Math.ceil(props.totalCount / props.pageSize);
    for (let i = 1; i <= pagesCount; i++) {
        pagesButtons.push(i);
    }

    pagesButtons = pagesButtons.map(p => {
        return (<button onClick={() => {
            props.onPageChange(p)
        }} className={u.pagesBtn + " " + (props.currentPage === p && u.selectedPage)}>{p}</button>)
    });


    let isFollowed = (followed) => {
        switch (followed) {
            case true:
                return 'Unfollow'
            case false:
                return 'Follow'
            default:
                return ''
        }
    }
    const userCards = props.userInfo.map(us => <UserCard follow={props.follow}
                                                              unfollow={props.unfollow} userName={us.name}
                                                              userId={us.id} status={us.status}
                                                              userIcon={us.avatar != undefined ? us.avatar : userIcon}
                                                              followed={isFollowed(us.followed)}/>);
    return (
        <div>
            <div>
                <div className={u.container}>
                    {userCards}
                </div>
            </div>
            <div className={u.btns_block}>
                {pagesButtons}
            </div>
        </div>
    )
}

export default Users;