import React from 'react';
import {Route} from "react-router-dom";
import mn from './Main.module.css'
import UserMiniCard from "./Reusable Components/UserMIniCard/UserMiniCard";
import SuperDialogsContainer from "./Dialogs/DialogsContainer";
import UsersContainer from "./Users/UsersContainer";
import DashboardContainer from "./Dashboard/DashboardContainer";


const Main = (props) => {
    const usersMiniCard = props.state.sidebars.otherUsersSidebar.map(usCard => <UserMiniCard userId={usCard.userId}
                                                                                             userName={usCard.userName}
                                                                                             userIcon={usCard.userIcon}/>);

    return (
        <div className={mn.main}>
            <div className="wrap">
                <Route path='/dashboard' render={() => <DashboardContainer usersMiniCard={usersMiniCard}/>}/>
                <Route path='/messeges' render={() => <SuperDialogsContainer usersMiniCard={usersMiniCard}/>}/>
                <Route path='/friends' render={() => <UsersContainer/>}/>
            </div>
        </div>
    )
}

export default Main;