import React from 'react';
import '../../App.css';
import SuperAddPostBlockContainer from "./AddPostBlock/AddPostBlockContainer";
import Preloader from "../Reusable Components/Preloader/Preloader";


const Dashboard = (props) => {


    if (!props) {
        return (
            <Preloader/>
        )
    }

    return (
        <div>
            <div className="container space-between">
                <div className='left__block'>
                    <div className='sidebar vh-45'>
                        <h4 className='sideBar-title'>
                            User Information
                        </h4>
                        <div className='sideBarBody'>
                            <p>
                                <span>User Name:</span> {}.
                            </p>
                            <p>
                                <span>Age:</span> {/*{props.userData.id1.age}*/} years.
                            </p>
                            <p>
                                <span>Date of Birth:</span> {/*{props.userData.id1.dateOfBirth}*/}.
                            </p>
                            <p><span>Date of Registration:</span> <br/> {/*{props.userData.id1.dateOfReg}*/}.</p>
                            <p><span>Kind of work:</span> {/*{props.userData.id1.kindOfWork}*/}.</p>

                        </div>
                    </div>
                    <div className='sidebar vh-45'>
                        <h4 className='sideBar-title'>
                            People You may know
                        </h4>
                        <div className='sideBarBody'>
                            {props.usersMiniCard}
                        </div>
                    </div>
                </div>
                <div className='main__block'>
                    <SuperAddPostBlockContainer />
                </div>
            </div>
        </div>
    )
}

export default Dashboard;
