import React from 'react';
import apb from './AddPostBlock.module.css';
import Post from "./Post/Post";
import {SuperSendBlockContainer} from "../../Reusable Components/SendBlock/SendBlockContainer";


const AddPostBlock = (props) => {
    const postItems = props.postsData.map(post => <Post userName={post.userName} userIcon={post.userIcon} postText={post.postText} postDate={post.postDate}/>);

    let btnName = 'Make a Post'

    return (
        <div>
            <div className={apb.type__post__block}>
                <SuperSendBlockContainer sign='POST' dispatch={props.dispatch} newText={props.newPostText} className='mt-20' btnName={btnName}/>
            </div>
            <div className={apb.posts__block}>
                {
                    postItems
                }
            </div>
        </div>
    )
}
export default AddPostBlock;
