import React from "react";
import uc from './UserCard.module.css';
import email from '../../../img/mail.png';
import {NavLink} from "react-router-dom";

const UserCard = (props) => {


    let changeFollowing = () => {
        if (props.followed == 'Unfollow') {
            props.unfollow(props.userId);
        }else if (props.followed == 'Follow') {
            props.follow(props.userId);
        }
    }


    return (
        <div className={uc.card}>
            <NavLink to='/dashboard'>
                <div className={uc.avatar}>
                    <img src={props.userIcon}/>
                </div>
            </NavLink>
            <h3 className={uc.userName}>{props.userName}</h3>
            <h4 className={uc.userStatus}>{props.status}</h4>
            <div className={uc.btns_block}>
                <button onClick={changeFollowing} className={uc.f_btn}>{props.followed}
                </button>
                <button className={uc.m_btn}><img src={email}/></button>
                <button className={uc.h_btn}>Hire</button>
            </div>
            <div className={uc.card_footer}>
                <a href='#'>View Profile</a>
            </div>
        </div>
    )
}

export default UserCard;