let ADD_POST = 'ADD-POST';
let TYPE_POST = 'TYPE-POST';
let SET_USERS_PROFILE = 'SET_USERS_PROFILE';
let initialSate =  {
    profileInfo: null,
    postsData: [
        {
            postText: 'Hello my dear friends!',
            postDate: '11.04.2020 22:00',
            postId: 1,
            userName: "Roman Venhryn",
            userIcon: "https://image.flaticon.com/icons/png/512/21/21104.png",
        },
        {
            postText: 'Hi everyone! It is nice to see you here',
            postDate: '11.04.2020 22:00',
            postId: 2,
            userName: "Dima Morda",
            userIcon: "https://image.flaticon.com/icons/png/512/21/21104.png",
        },
        {
            postText: 'Doctor is here.',
            postDate: '11.04.2020 22:00',
            postId: 3,
            userName: "Valentyn Berdnik",
            userIcon: "https://image.flaticon.com/icons/png/512/21/21104.png",
        },
        {
            postText: 'I am a professional car mechanic',
            postDate: '11.04.2020 22:00',
            postId: 4,
            userName: "Mykola Ryduk",
            userIcon: "https://image.flaticon.com/icons/png/512/21/21104.png",
        }
    ],
    newPostText: "Dimich",
    button: "Make a Post",
};

let dashboardReducer = (state = initialSate, action) => {
    switch (action.type) {
        case ADD_POST: {
            let newPost = {
                postText: state.newPostText,
                postDate: '11.04.2020 22:00',
                postId: 1,
                userName: "Roman Venhryn",
                userIcon: "https://image.flaticon.com/icons/png/512/21/21104.png",
            }
            return {
                ...state,
                newPostText: '',
                postsData: [...state.postsData, newPost]
            }
            /*let stateCopy = {...state};
            stateCopy.postsData = [...state.postsData];
            stateCopy.postsData.push(newPost);
            return stateCopy;*/
        }
        case TYPE_POST:
            /*let stateCopy = {...state};
            stateCopy.newPostText = action.newText;
            return stateCopy;*/
            return {
                ...state,
                newPostText: action.newText,
            }
        case SET_USERS_PROFILE:
            return {
                ...state,
                profileInfo: action.profile,
            }
        default: return state;
    }
};

export const addPostActionCreator = () => ({type: ADD_POST});
export const typePostTextActionCreator = (text) => ({type: TYPE_POST, newText: text});
export const setUsersProfile = (profile) => ({type: SET_USERS_PROFILE, profile})

export default dashboardReducer;
