import React from 'react';
import user from '../../../img/user.jpg';
import un from './UserName.module.css';

const UserName = () => {
    return (
        <div className={un.block}>
            <p>Dima  Borodin</p>
            <img className={un.mini__user__img}  src={user} alt=""/>
        </div>
    )
}

export default UserName;