import React from "react";
import p from './Post.module.css';


const Post = (props) => {
    return (
        <div className={p.postBlock}>
            <p className={p.postText}>
                {props.postText}
            </p>
            <div className={p.postData}>
                <div className={p.userName}>
                    <img src={props.userIcon} alt=""/>
                    <span>{props.userName}</span>
                </div>
                <p className={p.postDate}>
                    {props.postDate}
                </p>
            </div>
        </div>
    )
}

export default Post;
