import React from 'react';
import '../../App.css';
import Dashboard from "./Dashboard";
import * as axios from "axios" ;
import {connect} from "react-redux";
import {setUsersProfile} from "../../Redux/dashboard-reducer";
import Preloader from "../Reusable Components/Preloader/Preloader";


class DashboardAPIContainer extends React.Component {
    componentDidMount() {
        axios.get(`https://social-network.samuraijs.com/api/1.0/profile/2`).then(response => {
            this.props.setUsersProfile(response.data);
        })
        console.log(this.props);
    }
     render() {
        return (
            <Dashboard props={this.props.profileInfo}/>
        )
     }



}

let mapStateToProps = (state) => {
    return {
        profileInfo: state.dashboardPage.profileInfo,
    }
}

const DashboardContainer = connect(mapStateToProps, {setUsersProfile,})(DashboardAPIContainer);
export default DashboardContainer;
