import React from 'react';
import sf from './SearchForm.module.css';
import srch from '../../../img/loupe.png'
import '../../../App.css';
const SearchForm = () => {
    return (
        <div className={sf.block}>
            <img src={srch} alt=""/>
            <input type="text"/>
        </div>
    )
}

export default SearchForm;