import React from "react";
import {connect} from "react-redux";
import {
    follow,
    toggleIsFetching,
    setPageNumber,
    setTotalCount,
    setUsers,
    unfollow
} from "../../Redux/users-reducer";
import Users from "./Users";
import * as axios from "axios";
import preloader from '../../img/5.gif';
import Preloader from "../Reusable Components/Preloader/Preloader";


class UsersAPIComponent extends React.Component {

    constructor(props) {
        super(props);
    }

    componentDidMount() {
        debugger;
        this.props.toggleIsFetching(true);
        axios.get(`https://social-network.samuraijs.com/api/1.0/users?count=${this.props.pageSize}&page=${this.props.currentPage}`).then(response => {
            this.props.toggleIsFetching(false);
            this.props.setUsers(response.data.items);
            let totalCount = response.data.totalCount - 6200;
            this.props.setTotalCount(totalCount);
        })
    }

    onPageChange = (pageNumber) => {
        this.props.toggleIsFetching(true);
        this.props.setPageNumber(pageNumber);
        axios.get(`https://social-network.samuraijs.com/api/1.0/users?count=${this.props.pageSize}&page=${pageNumber}`).then(response => {
            this.props.toggleIsFetching(false);
            this.props.setUsers(response.data.items);
            /* this.props.setTotalCount(response.data.totalCount) */
        })
    }

    render() {
        return (
            <div>
                <Preloader isFetching={this.props.isFetching}/>
                <Users onPageChange={this.onPageChange}
                       follow={this.props.follow}
                       unfollow={this.props.unfollow}
                       userInfo={this.props.userInfo}
                       pageSize={this.props.pageSize}
                       currentPage={this.props.currentPage}
                       totalCount={this.props.totalCount}

                />
            </div>

        )
    }
}

let mapStateToProps = (state) => {
    return {
        userInfo: state.usersInfo.userInfo,
        pageSize: state.usersInfo.pageSize,
        totalCount: state.usersInfo.totalCount,
        currentPage: state.usersInfo.currentPage,
        isFetching: state.usersInfo.isFetching,
    }
}

/*let mapDispatchToProps = (dispatch) => {
    return {
        follow: (id) => {
            dispatch(followAC(id))
        },
        unfollow: (id) => {
            dispatch(unfollowAC(id))
        },
        setUsers: (users) => {
            dispatch(setUsersAC(users))
        },
        setPageNumber: (number) => {
            dispatch(setPageNumberAC(number));
        },
        setTotalCount: (totalCount) => {
            dispatch(setTotalCountAC(totalCount));
        },
        toggleIsFetching: (boolen) => {
            dispatch(isFetchingAC(boolen));
        }
    }
}*/

const UsersContainer = connect(mapStateToProps, {
    follow, unfollow, setUsers, setPageNumber, setTotalCount, toggleIsFetching,
})(UsersAPIComponent);

export default UsersContainer;