import React from "react";
import d from './Dialogs.module.css';
import Message from "./Message/Message";
import {SuperSendBlockContainer} from "../Reusable Components/SendBlock/SendBlockContainer";


const Dialogs = (props) => {
    const messageItems = props.messagesData.map(message => <Message messageText={message.messageText} userIcon={message.userIcon} messageDate={message.messageDate}/>);
    let btnName = 'Send a Message'

    return (
        <div>
            <div className="container space-between">
                <div className="left__block">
                    <div className="sidebar vh-70">
                        <h4 className="sideBar-title">
                            Dialogs List
                        </h4>
                        <div className='sideBarBody'>
                            {props.usersMiniCard}
                        </div>
                    </div>
                </div>
                <div className="main__block">
                    <div className={d.messeges__block}>
                        {messageItems}
                    </div>
                    <div className={d.type__mess_block}>
                        <SuperSendBlockContainer newText={props.newMessageText} sign="MESSAGE" className='mt-20'  btnName={btnName}/>
                    </div>
                </div>
            </div>
        </div>

    )
}

export default Dialogs;